.technical-about {
  padding-top: 20px;
  padding-bottom: 20px;
}

.technical-about h1 {
  margin: 0;
  font-weight: bold;
  font-size: 40px;
  line-height: 140%;
}

.technical-about p {
  margin: 0;
  margin-top: 20px;
  font-size: 18px;
  line-height: 140%;
}

@media (max-width: $mobile-width) {
  .technical-about h1 {
    font-size: 24px;
  }

  .technical-about p {
    font-size: 16px;
  }
}
